main {
    min-height: auto;
    height: auto;
    width: 70%;

    margin: 4rem 0 0;

    display: flex;
    flex-direction: column;
    align-items: stretch;
}

/* Links */
main a.link {
    border-radius: 5px;
    background: white;
    padding: 1px;
    margin: 1px;
    position: relative;

    text-decoration: none;

    color: var(--dark-blue);

    transition: background-color, color 0.2s ease-in-out;
}

main a.link:hover {
    background: var(--dark-blue);
    color: white;
}

main a.link:after {
    content: "\f35d";
    font-family: "Font Awesome 6 Free";
    font-weight: 900;
    font-size: 0.7rem;
    text-decoration: none;

    /* make sure the icon floats above the text */
    position: relative;
    top: -0.2rem;

}

main * {
    overflow: hidden;
}

/* Figures */
figure {
    float: left;
    margin: 1rem 2rem 1rem 2rem;
    padding: 0;
    text-align: center;
    background: rgba(238, 238, 238, 0.7);

    box-shadow: 0 0 10px 0 rgba(0, 0, 0, 0.5);

    border-radius: 10px;
    color: black;

    display: flex;
    flex-direction: column;
}

.floatl {
    float: left;
}

.floatr {
    float: right;
}

figure img {
    border-radius: 10px;

    width: auto;
    height: auto;

    object-fit: contain;
}

figure figcaption {
    display: inline;
    font-weight: bold;
    line-height: 2rem;
    vertical-align: center;
    padding-left: 1rem;
    padding-right: 1rem;
}

/* Underline under the header of the headers */
h1:after, h2:after, h3:after {
    content: "";
    display: block;
    width: 60%;
    height: 3px;
    background: white;
    margin-top: 10px;
}

.no_float {
    clear: both;
}

h1 {
    text-align: center;
    margin-left: auto;
    margin-right: auto;
}

/* A thicker underline under h1 */
h1:after {
    width: 100%;
    height: 5px;
}

li {
    /* to fix disc being cut off */
    overflow: visible;
}

img[enlargeable] {
    cursor: zoom-in;
}

/* make the images shrink and disable the float */
@media screen and (max-width: 1300px) {

    figure {
        margin: 0;
        padding: 0.1rem;
        max-width: 100% !important;
    }

    section {
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        justify-content: stretch;
    }

    .floatl, .floatr {
        float: none;
        align-self: center;
        justify-self: center;
    }

    main {
        min-height: auto;
        height: auto;
        width: 90%;

        margin-top: 4rem;

        display: flex;
        flex-direction: column;
    }

}